
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}
.background-img {
  position: absolute;
  width: 120%;
  height: auto;
  margin-top: -10%;
  margin-left: -10%;
  background-size: cover;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  -webkit-filter: blur(10px);
          filter: blur(10px);
  -webkit-transition: -webkit-transform 2s ease-in-out;
  transition: -webkit-transform 2s ease-in-out;
  transition: transform 2s ease-in-out;
  transition: transform 2s ease-in-out, -webkit-transform 2s ease-in-out;
}
.background-img-open {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.disc {
  position: relative;
  margin-top: 10%;
  margin-left: 10%;
  width: 25rem;
  height: 25rem;
  border-radius: 25rem;
  -webkit-transform: scale(0.7) rotate(45deg);
          transform: scale(0.7) rotate(45deg);
  background: #000;
  background-image: radial-gradient(5em 30em ellipse, #fff, #000);
  border: 2px solid #131313;
  -webkit-box-shadow: 0 0 0 10px #343935;
          box-shadow: 0 0 0 10px #343935;
  opacity: 0.7;
  cursor: pointer;
}
.disc-open {
  margin-top: 15%;
  margin-left: 30%;
  -webkit-transform: scale(1) rotate(45deg);
          transform: scale(1) rotate(45deg);
}
input[type=range] {
  -webkit-appearance: none;
  height: 8px;
  border-radius: 10px;
  background-color: #fff;
}
.song-img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  -webkit-transform: translate(-50%, -50%) rotate(0deg);
          transform: translate(-50%, -50%) rotate(0deg);
  width: 15rem;
  height: 15rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border: 2px solid #000;
  border-radius: 15rem;
  -webkit-animation: animations1 10s linear infinite forwards;
          animation: animations1 10s linear infinite forwards;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  background-image: url(/static/img/head.eeeb5a2.jpeg);
}
.lrc {
  position: absolute;
  right: 10%;
  top: 10%;
  overflow: auto;
  width: 40%;
  height: 50%;
  color: #fff;
  -webkit-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
}
.lrc-hide {
  right: 2%;
  opacity: 0;
}
.lrc-active {
  color: #f00;
  font-size: 1.6rem;
  -webkit-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
}
.content {
  position: absolute;
  bottom: -2rem;
  width: 100%;
  color: #fff;
  opacity: 0.1;
  -webkit-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
}
.content-open {
  bottom: 3rem;
  opacity: 1;
  font-size: 1.5rem;
  -webkit-transform: rotateX(-360deg);
          transform: rotateX(-360deg);
}
.mj-title {
  position: fixed;
  color: rgba(178,178,178,0.98);
  bottom: 2rem;
  left: 10rem;
  text-decoration: none;
  font-size: 2rem;
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
.mj-title-open {
  font-size: 1rem;
}
.home-wrapper {
  position: relative;
  top: 400px;
  background-color: #2c3e50;
  opacity: 1;
  -webkit-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
  visibility: hidden;
  -webkit-transform: rotateY(-90deg);
          transform: rotateY(-90deg);
}
.home-wrapper-open {
  opacity: 1;
  top: 200px;
  visibility: visible;
  -webkit-transform: rotateY(0);
          transform: rotateY(0);
}
.home {
  position: absolute;
  left: 47%;
  bottom: 20%;
  width: 6rem;
  height: 6rem;
  border-radius: 6rem;
  background-color: rgba(0,0,0,0.25);
  cursor: pointer;
  -webkit-box-shadow: 0 0 10px 1px rgba(0,0,0,0.55);
          box-shadow: 0 0 10px 1px rgba(0,0,0,0.55);
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.home-icon {
  position: absolute;
  width: 3rem;
  height: 3rem;
  border-radius: 3rem;
  bottom: 1.5rem;
  left: 48.7%;
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
.home-icon-hover {
  left: 44%;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.home-hover {
  position: absolute;
  left: 42%;
  bottom: 22%;
  width: 16rem;
  height: 6rem;
  border-radius: 6rem;
  background-color: rgba(0,0,0,0.45);
  -webkit-box-shadow: 0 0 100px 1px rgba(0,0,0,0.55);
          box-shadow: 0 0 100px 1px rgba(0,0,0,0.55);
  text-align: left;
  line-height: 6rem;
  cursor: pointer;
}
.home-content {
  position: absolute;
  left: 50%;
  height: 3.5rem;
  opacity: 0;
  bottom: 10%;
  text-decoration: none;
  color: #ef0f4e;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}
.home-content-hover {
  opacity: 1;
  font-size: 1.2rem;
}
.voice {
  position: fixed;
  bottom: 5rem;
  right: 10rem;
  width: 15rem;
}
@-webkit-keyframes animations1 {
0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
}
50% {
    -webkit-transform: translate(-50%, -50%) rotate(180deg);
            transform: translate(-50%, -50%) rotate(180deg);
}
100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg);
}
}
@keyframes animations1 {
0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
}
50% {
    -webkit-transform: translate(-50%, -50%) rotate(180deg);
            transform: translate(-50%, -50%) rotate(180deg);
}
100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg);
}
}
.fa-icon {
  display: inline-block;
  fill: currentColor;
}
.fa-flip-horizontal {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.fa-flip-vertical {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
}
.fa-spin {
  -webkit-animation: fa-spin 1s 0s infinite linear;
          animation: fa-spin 1s 0s infinite linear;
}
.fa-inverse {
  color: #fff;
}
.fa-pulse {
  -webkit-animation: fa-spin 1s infinite steps(8);
          animation: fa-spin 1s infinite steps(8);
}
@-webkit-keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
